.test {
  background: radial-gradient(
    circle,
    rgb(86, 90, 27) 0%,
    rgb(67, 70, 33) 30%,
    rgb(19, 17, 31) 100%
  );
  height: 100vh;
  color: #efefef;
}
.particles {
  position: relative;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
}
.particles span {
  position: absolute;
  top: 10%;
  left: 10%;
  display: block;
  content: "";
  width: 6px;
  height: 6px;
  background: rgba(255, 255, 255, 0.6);
  border-radius: 0.5rem;
  filter: blur(5px);
}
.particles span:nth-child(2) {
  top: 15%;
  left: 70%;
  filter: blur(3px);
}
.particles span:nth-child(3) {
  top: 70%;
  left: 40%;
  filter: blur(5px);
}
.particles span:nth-child(4) {
  top: 52%;
  left: 20%;
  filter: blur(4px);
}
.particles span:nth-child(5) {
  top: 74%;
  left: 90%;
  filter: blur(5px);
}
.particles span:nth-child(6) {
  top: 85%;
  left: 10%;
  filter: blur(7px);
}
.particles span:nth-child(7) {
  top: 67%;
  left: 79%;
  filter: blur(3px);
}
.particles span:nth-child(8) {
  top: 48%;
  left: 40%;
  filter: blur(4px);
}
.particles span:nth-child(9) {
  top: 45%;
  left: 30%;
  filter: blur(5px);
}
.particles span:nth-child(10) {
  top: 96%;
  left: 29%;
  filter: blur(4px);
}
.particles span:nth-child(11) {
  top: 55%;
  left: 89%;
  filter: blur(6px);
}
.particles span:nth-child(12) {
  top: 55%;
  left: 60%;
  filter: blur(7px);
}
.mainSection {
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-family: cursive;
}
.mainSection h1 {
  font-size: 3rem;
}
.mainSection h1 {
  text-align: center;
  text-shadow: 0 0 5px #c3d168a2;
}
.mainSection div {
  margin-top: 2rem;
  text-align: center;
}
.mainSection div span {
  font-size: 7rem;
  line-height: 6rem;
  text-shadow: 0 0 7px #c3d168a2;
}
.circle {
  user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-select: none;
  display: inline-block;
  position: relative;
  width: 7rem;
  height: 7rem;
  text-shadow: none;
  background: #e6f1a3 radial-gradient(#f9ffd2, #ecff70);
  color: rgba(0, 0, 0, 0);
  border-radius: 50%;
  box-shadow: 0 0 7px #e7f1a3a2;
}
.circle:after {
  display: block;
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
  width: 9rem;
  height: 5rem;
  border-radius: 50%;
  border: 2px solid #fafafa;
  border-top: 0px solid #fafafa;
  border-bottom: 4px solid #fafafa;
  z-index: 2;
}
.circle:before {
  display: block;
  position: absolute;
  content: "";
  top: 50%;
  left: 50%;
  background: #121212;
  border-radius: 50%;
  width: 8px;
  height: 8px;
  transform-origin: 2.5rem 0;
  transform: translate(-2.5rem, 0) rotate(0deg);
  animation: circle-around 5s infinite linear;
}
@keyframes circle-around {
  0% {
    transform: translate(-2.5rem, 0) rotate(0deg);
  }
  100% {
    transform: translate(-2.5rem, 0) rotate(360deg);
  }
}
.mainSection p {
  font-size: 1.5rem;
  margin-top: 2rem;
  text-align: center;
  text-shadow: 0 0 5px #c3d168a2;
}
